<template>
  <div class="technics-card">
    <p class="technics-card__category">  / </p>
    <p class="technics-card__title">{{title}}</p>
    <p class="technics-card__price"> 
      <span class="technics-card__price-num">{{price}} </span>
    </p>
    <div class="technics-card__row">
      <div class="technics-card__img-col">
        <div class="technics-card__image">
          <img :src="imagePath" alt="">
        </div>
      </div>
      <div class="technics-card__side-col">
        <div class="technics-card__characteristics">
          <p class="technics-card__characteristic">
            <span class="technics-card__characteristic-icon">
              <Icon name="fire"/>
            </span>
             
          </p>
          <p class="technics-card__characteristic">
            <span class="technics-card__characteristic-icon">
              <Icon name="circles"/>
            </span>
            4 
          </p>
          <p class="technics-card__characteristic">
            <span class="technics-card__characteristic-icon">
              <Icon name="materials"/>
            </span>
            
          </p>
          <p class="technics-card__characteristic">
            <span class="technics-card__characteristic-icon">
              <Icon name="size"/>
            </span>
            600500 
          </p>
        </div>
        <a href="#" class="technics-card__btn">
          
          <span class="technics-card__btn-icon">
            <img src="../assets/img/arrow-right-gradient.svg" alt="">
          </span>
        </a>
      </div>      
    </div>
  </div>
</template>

<script>
import Icon from './base/Icon'

export default {
  name: 'TechnicsCard',
  components: {
    Icon
  },
  props: {
    image: String,
    title: String,
    price: String
  },
  computed: {
    imagePath() {
      let img = require('@/assets/img/' + this.image)
      return img
    }
  }
}
</script>

<style lang="scss">
.technics-card {
  border-top: 1px solid #D9D9D9;
  padding-top: 20px;

  &__category {
    font-size: 10px;
    line-height: (12/10);
    text-decoration: underline;
    color: $color-gray-middle;
  }

  &__title {
    margin-top: 11px;
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 18px;
    line-height: (29/18);
    color: $color-blue;
  }

  &__price {
    margin-top: 3px;
    font-weight: bold;
    font-size: 12px;
    line-height: (20/12);
    color: $color-blue;
  }

  &__price-num {
    font-size: 30px;
  }

  &__row {
    margin-top: 4px;
    display: flex;
  }

  &__img-col {
    width: 53%;
  }

  &__image {
    position: relative;
    padding-top: 93%;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__side-col {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-left: 28px;
    margin-top: 10px;
  }

  &__characteristics {
    margin-bottom: 12px;
  }

  &__characteristic {
    display: flex;
    align-items: center;
    margin-bottom: 13px;
    font-size: 10px;
    line-height: (16/10);
    color: $color-gray-middle;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__characteristic-icon {
    flex-shrink: 0;
    width: 12px;
    height: 12px;
    margin-right: 8px;
  }

  &__btn {
    display: flex;
    align-items: center;
    margin-top: auto;
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 16px;
    line-height: (24/16);
    text-decoration: underline;
    color: $color-blue;
  }

  &__btn-icon {
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    margin-left: 14px;
  }

  @include media(xl) {
    &__img-col {
      width: 55%;
    }

    &__btn {
      font-size: 12px;
      line-height: (18/12);
    }

    &__btn-icon {
      margin-left: 7px;
      width: 14px;
      height: 14px;
    }
  }
}
</style>
