<template>
  <div class="nav-panel">
    <template v-if="catalog || technics || blog">
      <Pagination class="nav-panel__pagination" />
      <div class="nav-panel__group nav-panel__group_wide">
        <RouterLink
          to="/about"
          v-slot="{ href, navigate }"
        >
          <Link
            :href="href"
            class="nav-panel__item"
            text=""
            @click.native="navigate"
          />
        </RouterLink>
        <RouterLink
          to="/delivery"
          v-slot="{ href, navigate }"
        >
          <Link
            :href="href"
            class="nav-panel__item"
            text="    "
            @click.native="navigate"
          />
        </RouterLink>
        <Link class="nav-panel__item" text="  " @click.prevent.native="showModal" />
      </div>
    </template>
    <template v-if="product || article">
      <div class="nav-panel__group">
        <Link class="nav-panel__item" text="  "/>
        <Link class="nav-panel__item" text="    "/>
      </div>
      <div class="nav-panel__group">
        <Link class="nav-panel__item" text="   "/>
      </div>
    </template>
    <template v-if="about || payment || reviews">
      <div class="nav-panel__group">
        <RouterLink
          to="/payment"
          v-slot="{ href, navigate }"
        >
          <Link
            :href="href"
            class="nav-panel__item"
            text="  "
            @click.native="navigate"
          />
        </RouterLink>
        <RouterLink
          to="/delivery"
          v-slot="{ href, navigate }"
        >
          <Link
            :href="href"
            class="nav-panel__item"
            text="    "
            @click.native="navigate"
          />
        </RouterLink>
      </div>
      <div class="nav-panel__group">
        <Link class="nav-panel__item" text=" "/>
      </div>
    </template>    
    <template v-if="delivery">
      <div class="nav-panel__group">
        <Link class="nav-panel__item" text="  "/>
        <Link class="nav-panel__item" text="    "/>
      </div>
      <div class="nav-panel__group">
        <Link class="nav-panel__item" text="  "/>
      </div>
    </template>
  </div>
</template>

<script>
import Pagination from './base/Pagination'
import Link from './base/Link'

export default {
  name: 'NavPanel',
  components: {
    Pagination,
    Link
  },
  props: {
    catalog: Boolean,
    product: Boolean,
    about: Boolean,
    delivery: Boolean,
    payment: Boolean,
    technics: Boolean,
    reviews: Boolean,
    blog: Boolean,
    article: Boolean
  },
  methods: {
    showModal() {
      this.$store.commit('setModal', 'call')
    }
  }
}
</script>

<style lang="scss">
.nav-panel {
  $b: &;

  &__group {
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }
  }
  
  &__pagination {
    width: 100%;
    margin-bottom: 56px;
  }

  &__item {
    margin-bottom: 32px;
    color: $color-blue;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include media(md) {
    &__pagination {
      margin-bottom: 70px;
    }
  }

  @include media(lg) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    &__pagination {
      margin-bottom: 60px;
    }

    &__group {
      display: flex;
      align-items: center;
      margin-bottom: 0;

      &_wide {
        width: 100%;

        #{$b}__item:last-child {
          margin-left: auto;
        }
      }
    }

    &__item {
      margin-right: 70px;
      margin-bottom: 0;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  @include media(xl) {
    &__pagination {
      width: auto;
      margin-bottom: 0;
    }

    &__group {
      &_wide {
        width: auto;
      }
    }

    &__item {
      margin-right: 65px;
    }
  }
}
</style>
