<template>
  <div class="catalog-card" :class="[{'catalog-card_high': high}, {'catalog-card_alt': alt}, {'catalog-card_top': top}]">
    <p class="catalog-card__category" v-if="mobile || (!alt && !top)">  / </p>
    <RouterLink to="/product" class="catalog-card__images">
      <Swiper
        :options="swiperOptions"
        ref="mySwiper"
        class="catalog-card__slider"
      >
        <SwiperSlide class="catalog-card__item">
          <div class="catalog-card__image anim-img js-anim">
            <img :src="imagePath" alt="">
          </div>
        </SwiperSlide>
        <SwiperSlide class="catalog-card__item">
          <div class="catalog-card__image">
            <img :src="imagePath" alt="">
          </div>
        </SwiperSlide>
        <SwiperSlide class="catalog-card__item">
          <div class="catalog-card__image">
            <img :src="imagePath" alt="">
          </div>
        </SwiperSlide>
        <ButtonNav
          prev
          :sm="alt"
          :xs="!alt"
          class="catalog-card__prev"
          slot="button-prev"  
        />
        <ButtonNav
          :sm="alt"
          :xs="!alt"
          class="catalog-card__next"
          slot="button-next"  
        />
      </Swiper>
      <p v-if="discount" class="catalog-card__label">   </p>
      <Discount v-if="discount" class="catalog-card__discount"/>
      <button type="button" class="catalog-card__btn" @click.prevent="showModal">
        
        <span class="catalog-card__btn-icon">
          <img src="../assets/img/arrow-right-gradient.svg" alt="">
        </span>
      </button>
    </RouterLink>
    <div class="catalog-card__content">
      <div class="catalog-card__info">
        <p class="catalog-card__category" v-if="!mobile && (alt || top)">  / </p>
        <p class="catalog-card__title">{{title}}
          <span class="catalog-card__stat">
            <span class="catalog-card__stat-icon">
              <Icon name="likes"/>
            </span>
            157
          </span>
        </p>
      </div>
      <div class="catalog-card__prices">
        <p v-if="oldprice" class="catalog-card__oldprice">{{oldprice}} </p> 
        <p class="catalog-card__price"> 
          <span class="catalog-card__price-num">{{price}} </span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import Discount from './base/Discount'
import Icon from './base/Icon'
import ButtonNav from './base/ButtonNav'
import { swiper as Swiper, swiperSlide as SwiperSlide } from 'vue-awesome-swiper'

export default {
  name: 'CatalogCard',
  components: {
    Discount,
    Icon,
    ButtonNav,
    Swiper,
    SwiperSlide
  },
  props: {
    high: Boolean,
    alt: Boolean,
    top: Boolean,
    image: String,
    title: String,
    price: String,
    oldprice: String,
    discount: String,
    similar: Boolean,
    animDelay: Number
  },
  data() {
    return {
      swiperOptions: {
        resistanceRatio: 0,
        navigation: {
          prevEl: '.catalog-card__prev',
          nextEl: '.catalog-card__next'
        },
        allowTouchMove: !this.similar,
        breakpointsInverse: true,
        breakpoints: {
          [this.$breakpoints.xl]: {
            allowTouchMove: true
          }
        }
      }
    }
  },
  computed: {
    imagePath() {
      let img = require('@/assets/img/' + this.image)
      return img
    },
    mobile() {
      return this.$store.state.mobile
    },
    swiper() {
      return this.$refs.mySwiper.swiper
    }
  },
  methods: {
    showModal() {
      this.$store.commit('setModal', 'calc')
    }
  }
}
</script>

<style lang="scss">
.catalog-card {
  $b: &;

  border-top: 1px solid #D9D9D9;
  padding-top: 11px;

  &__category {
    font-size: 14px;
    line-height: (16/14);
    text-decoration: underline;
    color: $color-gray-middle;
  }

  &__images {
    display: block;
    position: relative;
    margin-top: 14px;
  }

  &__image {
    position: relative;
    padding-top: 75.3%;
    overflow: hidden;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__prev,
  &__next {
    position: absolute;
    bottom: 0;
    z-index: 1;
  }

  &__prev {
    left: 0;
  }

  &__next {
    left: 37px;
  }

  &__label {
    position: absolute;
    left: 24px;
    top: 20px;
    border-radius: 15px;
    padding: 5px 10px;
    font-family: $font-secondary;    
    font-size: 12px;
    line-height: (15/12);
    color: $color-white;
    background-color: $color-blue;
    z-index: 1;
  }

  &__discount {
    position: absolute;
    top: -18px;
    right: -14px;
    z-index: 1;
  }

  &__btn {
    display: flex;
    align-items: center;
    position: absolute;
    right: -1px;
    bottom: -1px;
    padding: 10px 1px 9px 20px;
    border-top-left-radius: 30px;
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 12px;
    line-height: 1.47;
    text-decoration: underline;
    color: $color-blue;
    background-color: #fff;
    z-index: 1;
  }

  &__btn-icon {
    flex-shrink: 0;
    width: 14px;
    height: 14px;
    margin-left: 8px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 17px;
  }

  &__info {
    margin-right: 20px;
  }

  &__title {
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 18px;
    line-height: (29/18);
    color: $color-blue;
  }

  &__stat {
    display: flex;
    align-items: center;
    font-weight: normal;
    font-size: 10px;
    line-height: (12/10);
    color: $color-gray-middle;
  }

  &__stat-icon {
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    margin-right: 8px;
  }

  &__prices {
    flex-shrink: 0;
    text-align: right;
  }

  &__oldprice {
    margin-top: -6px;
    font-size: 20px;
    line-height: (24/20);
    text-decoration: line-through;
    color: $color-gray-middle;
  }

  &__price {
    font-weight: bold;
    font-size: 12px;
    line-height: (24/20);
    color: $color-blue;
  }

  &__price-num {
    font-size: 30px;
  }

  @include media(lg) {
    padding-top: 20px;

    &_alt {
      border-top: none;
      padding-top: 0;
      padding-bottom: 14px;
      border-bottom: 1px solid #D9D9D9;

      #{$b} {
        &__images {
          margin-top: 0;
        }

        &__image {
          padding-top: 53.8%;
        }

        &__next {
          left: 42px;
        }

        &__content {
          margin-top: 20px;
        }

        &__category {
          font-size: 12px;
          line-height: (15/12);
        }

        &__title {
          display: flex;
          align-items: center;
          margin-top: 10px;
          margin-right: 0;
          font-size: 22px;
          line-height: (36/22);
        }

        &__stat {
          margin-top: 0;
          margin-left: 22px;
          margin-bottom: 0;
        }

        &__price {
          font-size: 22px;
          line-height: (36/22);
        }
      }
    }

    &_high {
      #{$b} {
        &__image {
          padding-top: 96.9%;
        }
      }

      &#{$b}_alt {
        #{$b}__image {
          padding-top: 100%;
        }
      }
    }

    &_top {
      border-bottom: none;

      #{$b} {
        &__image {
          padding-top: 75.3%;
        }

        &__content {
          padding-right: $container-padding-md;
        }

        &__btn {
          padding-right: $container-padding-md;
        }
      }
    }

    &__images {
      margin-top: 24px;
    }

    &__image {
      padding-top: 64.6%;
    }

    &__next {
      left: 38px;
    }

    &__btn {
      padding: 10px 0 8px 24px;
    }

    &__btn-icon {
      margin-left: 8px;
      margin-right: 8px;
      width: 18px;
      height: 18px;
    }

    &__category {
      font-size: 10px;
      line-height: (12/10);
    }

    &__content {
      margin-top: 16px;
    }

    &__title {
      margin-right: -30px;
      font-size: 18px;
      line-height: (29/18);
    }

    &__stat {
      margin-top: 10px;
      margin-bottom: 2px;
    }

    &__prices {
      margin-bottom: -2px;
    }

    &__oldprice {
      margin-left: 30px;
    }
  }

  @include media(xl) {
    &_high {
      #{$b} {
        &__image {
          padding-top: 102.9%;
        }
      }
    }

    &_alt {
      #{$b} {
        &__next {
          left: 48px;
        }
      }
    }

    &_top {
      #{$b} {
        &__btn {
          padding-right: $container-padding-xl;
        }

        &__content {
          padding-right: $container-padding-xl;
        }
      }
    }

    &__image {
      padding-top: 68.3%;
    }

    &__next {
      left: 42px;
    }

    &__discount {
      top: -15px;
    }
    
    &__stat {
      margin-top: 0;
      margin-bottom: 0;
    }
  }
}
</style>
