<template>
  <div class="tooltip">
    <div class="tooltip__point"></div>
    <div class="tooltip__box">
      <p class="tooltip__text"> {{'\xa0'}}</p>
      <div class="tooltip__image">
        <img src="../../assets/img/brand-01.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Tooltip'
}
</script>

<style lang="scss">
@keyframes wave {
  0% {
    transform: scale(1);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(3);
  }
}

.tooltip {
  $b: &;

  position: relative;

  &__point {
    position: relative;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    border: 7px solid $color-green;
    background-color: $color-white;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: -7px;
      top: -7px;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      border: 1px solid $color-white;
      opacity: 0;
      pointer-events: none;
    }

    &:hover {
      &::before {
        animation: wave 1s ease;
      }

      &::after {
        animation: wave 1s ease .25s;
      }

      & + #{$b}__box {
        opacity: 1;
      }
    }
  }

  &__box {
    position: absolute;
    top: 50%;
    right: 100%;
    margin-right: 10px;
    // margin-right: 14px;
    padding: 5px 8px;
    // padding: 8px 26px;
    background-color: $color-white;
    transform: translateY(-50%);
    opacity: 0;
    pointer-events: none;
    transition: opacity .3s ease;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 100%;
      margin-top: -7px;
      margin-left: -1px;
      border: 7px solid transparent;
      border-left-color: $color-white;
    }
  }

  &__text {
    // padding-right: 8px;
    font-size: 12px;
    line-height: (17/12);
  }

  &__image {
    display: inline-block;
    width: 53px;
  }

  @include media(md) {
    &__text {
      white-space: nowrap;
    }
  }

  @include media(lg) {
    &__box {
      padding: 9px 27px;
    }
  }
}
</style>
