<style lang="scss">
.preview {
  position: relative;
  font-family: $font-secondary;
  overflow: hidden;

  &__image {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
    }
  }

  &__content {
    position: relative;
    width: 100%;
    padding: 30px 24px 30px 20px;
    color: $color-white;
    transition-delay: .5s;
  }

  &__date {
    font-size: 14px;
    line-height: (17/14);
  }

  &__category {
    margin-top: 14px;
    font-size: 12px;
    line-height: (15/12);
    text-decoration: underline;
    color: $color-gray;
  }

  &__title {
    display: none;
    margin-top: 6px;
    font-weight: bold;
    font-size: 16px;
    line-height: (20/16);
  }

  &__desc {
    margin-top: 16px;
    padding-right: 10px;
    font-size: 12px;
    line-height: (19/12);
  }

  &__stats {
    display: flex;
    margin-bottom: 4px;
  }

  &__stat {
    display: flex;
    align-items: center;
    margin-right: 18px;
    font-size: 12px;
    line-height: (19/12);

    &:last-child {
      margin-right: 0;
    }
  }

  &__stat-icon {
    flex-shrink: 0;
    width: 16px;
    height: 16px;
    margin-right: 6px;
  }

  &__footer {
    display: flex;
