    }

    &__title {
      display: block;
    }

    &__desc {
      margin-top: 6px;
      font-size: 14px;
      line-height: (22/14);
    }

    &__footer {
      align-items: flex-start;
      margin-top: 16px;
    }

    &__stats {
      margin-bottom: 0;
    }

    &__btn {
      margin-top: 78px;
    }
