        <div class="detail-slider__tooltip">
          <Tooltip />
        </div>
        <div class="detail-slider__tooltip">
          <Tooltip />
        </div>
      </SwiperSlide>
      <ButtonNav
        prev
        class="detail-slider__prev"
        slot="button-prev"  
      />
      <ButtonNav
        next
        class="detail-slider__next"
        slot="button-next"  
      />
      <div
        class="detail-slider__pagination"
        slot="pagination"
      ></div>
    </Swiper>
    <Discount class="detail-slider__discount" />
    <ButtonAlt
      icon="home"
      text=" "
      class="detail-slider__btn"
      @click.native="showModal"
    />
  </div>
</template>

<script>
import Tooltip from './base/Tooltip'
import Discount from './base/Discount'
import ButtonAlt from './base/ButtonAlt'
import ButtonNav from './base/ButtonNav'
import { swiper as Swiper, swiperSlide as SwiperSlide } from 'vue-awesome-swiper'

export default {
  name: 'DetailSlider',
  components: {
    Tooltip,
    Discount,
    ButtonAlt,
    ButtonNav,
    Swiper,
    SwiperSlide
  },
  data: () => ({
    swiperOptions: {
      resistanceRatio: 0,
      navigation: {
        prevEl: '.detail-slider__prev',
        nextEl: '.detail-slider__next'
      },
      pagination: {
        el: '.detail-slider__pagination'
      }
    }
  }),
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    }
  },
  methods: {
    showModal() {
      this.$store.commit('setModal', 'call')
    }
  }
}
</script>

<style lang="scss">
.detail-slider {
  $b: &;

  position: relative;

  &__item {
    position: relative;
  }

  &__image {
    position: relative;
    padding-top: 101%;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__tooltip {
    position: absolute;

    &:nth-child(2) {
      left: 36.5%;
      top: 27%;
    }

    &:nth-child(3) {
      left: 83.6%;
      top: 37.8%;
    }

    &:nth-child(4) {
      left: 31.5%;
      top: 55%;
    }

    &:nth-child(5) {
      left: 76.2%;
      top: 68.2%;
    }
  }

  &__discount {
    position: absolute;
    top: 12px;
    right: 16px;
    // top: 36px;
    // right: 56px;
    z-index: 1;
  }

  &__btn {
    display: none;
    position: absolute;
    right: $container-padding-md;
    bottom: 52px;
    // right: 48px;
    // bottom: 70px;
    max-width: 144px;
    z-index: 1;
  }

  &__prev,
  &__next {
    display: none;
    position: absolute;
    bottom: 0;
    z-index: 1;
  }

  &__prev {
    left: 0;
  }

  &__next {
    left: 54px;
    // left: 62px;
  }

  .swiper-pagination-bullets {
    display: flex;
    justify-content: center;
    position: absolute;
    left: $container-padding;
    right: $container-padding;
    bottom: 18px;
    width: auto;
    z-index: 1;
  }

  .swiper-pagination-bullet {
    margin: 0 5px !important;
    width: 56px;
    height: 4px;
    border-radius: 4px;
    background-color: $color-white;
    opacity: 1;
  }

  .swiper-pagination-bullet-active {
    background-color: $color-green;
  }

  @include media(md) {
    &__image {
      padding-top: 60%;
    }

    &__discount {
      top: 18px;
      right: 30px;
    }

    &__prev,
    &__next {
      display: flex;
    }

    &__btn {
      display: inline-flex;
    }

    .swiper-pagination-bullets {
      left: auto;
      right: $container-padding-md;
      bottom: 22px;
    }
  }

  @include media(lg) {
    &__discount {
      top: 38px;
      right: 60px;
    }

    &__next {
      left: 62px;
    }

    &__btn {
      right: 52px;
      bottom: 74px;
    }

    .swiper-pagination-bullets {
      right: 48px;
      bottom: 38px;
    }
  }
}
</style>
