import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    windowWidth: null,
    mobile: null,
    modal: null
  },
  mutations: {
    storeScreen(state, {width, mobile}) {
      state.windowWidth = width
      state.mobile = mobile
    },
    setModal(state, modal) {
      state.modal = modal
    }
  }
})

export default store
