<template>
  <div class="similar-products">
    <h2 class="similar-products__heading">  {{'\xa0'}} </h2>
    <Swiper
      :options="swiperOptions"
      ref="mySwiper"
      class="similar-products__slider"
    >
      <SwiperSlide class="similar-products__item">
        <CatalogCard
          image="catalog-card-08.jpg"
          title=""
          price="85 500"
          oldprice="250 000"
          discount="-50%"
          similar
          v-anim="true"
        />
      </SwiperSlide>
      <SwiperSlide class="similar-products__item">
        <CatalogCard
          image="catalog-card-09.jpg"
          title=""
          price="85 500"
          similar
          v-anim="{delay: 100}"
        />
      </SwiperSlide>
      <SwiperSlide class="similar-products__item">
        <CatalogCard
          image="catalog-card-10.jpg"
          title=""
          price="85 500"
          oldprice="250 000"
          discount="-50%"
          similar
          v-anim="{delay: 200}"
        />
      </SwiperSlide>
      <SwiperSlide class="similar-products__item">
        <CatalogCard
          image="catalog-card-11.jpg"
          title=""
          price="85 500"
          similar
          v-anim="{delay: 300}"
        />
      </SwiperSlide>
    </Swiper>
  </div>
</template>

<script>
import CatalogCard from './CatalogCard'
import { swiper as Swiper, swiperSlide as SwiperSlide } from 'vue-awesome-swiper'

export default {
  name: 'SimilarProducts',
  components: {
    CatalogCard,
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      swiperOptions: {
        spaceBetween: 20,
        slidesPerView: 'auto',
        freeMode: true,
        freeModeMomentum: false,
        resistanceRatio: 0,
        breakpointsInverse: true,
        breakpoints: {
          [this.$breakpoints.md]: {
            spaceBetween: 50
          },
          [this.$breakpoints.xl]: {
            spaceBetween: 50,
            slidesPerView: 4,
            allowTouchMove: false
          }
        }
      }
    }
  }
}
</script>

<style lang="scss">
.similar-products {
  &__heading {
    font-size: 34px;
    line-height: (41/34);
  }

  &__slider {
    margin: 28px (-$container-padding) 0;
  }

  &__item {
    width: 336px;

    &:first-child {
      margin-left: 10px;
    }

    &:last-child {
      margin-right: 10px;
    }
  }

  @include media(md) {
    &__heading {
      font-size: 40px;
      line-height: (49/40);
    }

    &__slider {
      margin: 40px (-$container-padding-md) 0;
    }

    &__item {
      width: 410px;

      &:first-child {
        margin-left: $container-padding-md;
      }

      &:last-child {
        margin-right: $container-padding-md;
      }
    }
  }

  @include media(xl) {
    &__slider {
      margin: 40px 0 0;
    }
    
    &__item {
      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
</style>
