<template>
  <div id="app" class="app" :class="{'full-h': main}">
    <RouterView />
    <Footer v-if="!$mobile && !main" />
    <MobileBar class="app__mobile-bar" v-if="$mobile" />
    <Modal />
  </div>
</template>

<script>
import Footer from './components/Footer'
import MobileBar from './components/MobileBar'
import Modal from './components/Modal'

export default {
  name: 'App',
  components: {
    Footer,
    MobileBar,
    Modal
  },
  computed: {
    main() {
      return this.$route.name === 'Main'
    }
  },
  created() {
    this.storeScreen()
    window.addEventListener('resize', this.handleResize)
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    storeScreen() {
      this.$store.commit('storeScreen', {
        width: window.innerWidth,
        mobile: window.innerWidth < this.$breakpoints.lg
      })
    },
    handleResize() {
      this.storeScreen()
    }
  }
}
</script>

<style lang="scss">
.app {
  padding-bottom: $mobile-bar-height;
  overflow: hidden;

  &.full-h {
    padding-bottom: 0;
  }
  
  &__mobile-bar {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 10;
  }

  @include media(md) {
    padding-bottom: $mobile-bar-height-md;
  }

  @include media(lg) {
    padding-bottom: 0;
  }
}
</style>
