<template>
  <div class="dropdowns">
    <div
      v-for="(item, index) in items"
      :key="index"
      class="dropdowns__item"
      :class="['dropdown', {'dropdown_lg': item.lg}, {'is-active': activeItem === index}]"
    >
      <span class="dropdown__arrow" v-if="item.arrow">
        <Icon name="caret-right" />
      </span>
      <div class="dropdown__wrapper">
        <p class="dropdown__title" @click="onTitleClick(index)">{{item.title}}</p>
        <div class="dropdown__content" ref="content">
          <p class="dropdown__desc">{{item.desc}}</p>
        </div>
      </div>      
    </div>
  </div>  
</template>

<script>
import Icon from './Icon'

export default {
  name: 'Dropdown',
  components: {
    Icon
  },
  props: {
    items: Array
  },
  data: () => ({
    anim: false,
    activeItem: null
  }),
  methods: {
    onTitleClick(index) {
      if (this.anim) {
        return
      }

      this.anim = true

      const closingContent = this.activeItem === null ? null : this.$refs.content[this.activeItem]
      const openingContent = this.activeItem === index ? null : this.$refs.content[index]

      this.activeItem = openingContent ? index : null

      if (closingContent) {
        closingContent.style.height = closingContent.scrollHeight + 'px'

        setTimeout(() => {
          closingContent.style.height = ''
        }, 0)
      }

      if (openingContent) {
        openingContent.style.height = openingContent.scrollHeight + 'px'

        setTimeout(() => {
          openingContent.style.height = 'auto'
        }, 300);
      }

      setTimeout(() => {
        this.anim = false
      }, 300);
    }
  }
}
</script>

<style lang="scss">
.dropdowns {
  &__item {
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include media(md) {
    &__item {
      margin-bottom: 10px;
    }
  }
}

.dropdown {
  $b: &;

  display: flex;

  &.is-active {
    #{$b} {
      &__arrow {
        transform: rotate(90deg);
      }

      &__content {
        opacity: 1;
      }
    }
  }

  &_lg {
    #{$b} {
      &__title {
        font-size: 16px;
        line-height: (25/16);
      }

      &__desc {
        padding-top: 6px;
        line-height: (22/12);
        color: $color-gray-middle;
      }
    }
  }

  &__title {
    display: flex;
    position: relative;
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 12px;
    line-height: (20/12);
    color: $color-blue;
    cursor: pointer;
  }

  &__arrow {
    flex-shrink: 0;
    margin-left: -2px;
    margin-top: 4px;
    
    margin-right: 12px;
    width: 12px;
    height: 12px;
    transition: transform .3s ease;
  }

  &__content {
    height: 0;
    opacity: 0;
    overflow: hidden;
    transition: height .3s ease, opacity .3s ease;
  }

  &__desc {
    padding-top: 16px;
    font-size: 12px;
    line-height: (23/14);
  }

  @include media(md) {
    &_lg {
      #{$b} {
        &__title {
          font-size: 18px;
          line-height: (22/18);
        }
      }
    }

    &__title {
      font-size: 18px;
      line-height: (29/18);
    }

    &__arrow {
      margin-top: 6px;
      width: 17px;
      height: 17px;
      margin-right: 6px;
    }

    &__desc {
      padding-right: 70px;
      font-size: 14px;
    }
  }

  @include media(lg) {
    &__title {
      font-size: 14px;
      line-height: (23/14);
    }

    &__arrow {
      margin-right: 4px;
      width: 10px;
      height: 10px;
    }

    &__desc {
      padding-top: 4px;
      padding-right: 0;
      font-size: 12px;
      line-height: (20/12);
    }
  }

  @include media(xl) {
    &_lg {
      #{$b} {
        &__desc {
          max-width: none;
        }
      }
    }

    &__title {
      font-size: 18px;
      line-height: (29/18);
    }

    &__arrow {
      width: 17px;
      height: 17px;
    }

    &__desc {
      max-width: 260px;
      padding-top: 16px;
      font-size: 14px;
      line-height: (23/14);
    }
  }
}
</style>
