<template>
  <div class="details page-top">
    <div class="row">
      <div class="col col-12 col-lg-6" v-if="$windowWidth < $breakpoints.xl">
        <Header class="page-header fade-slide-down js-anim" v-anim="true" />
      </div>
      <div class="col col-12 col-xl-7 details__slider-col">
        <DetailSlider class="details__slider" v-anim="true" />
      </div>
      <div class="col col-12 col-lg-9 col-xl-5">
        <Header class="page-header fade-slide-down js-anim" v-anim="true" v-if="$windowWidth >= $breakpoints.xl" />
        <div class="details__wrapper fade-bounce-right js-anim" v-anim="true">
          <div class="details__nav">
            <RouterLink to="/catalog" v-slot="{ href, navigate }">
              <Back
                sm
                :href="href"
                class="details__back"
                @click.native="navigate"
              />
            </RouterLink>
            <p class="details__category">  / </p>
          </div>
          <h1 class="details__heading"> </h1>
          <div class="details__info">
            <div class="details__cost">
              <div class="details__prices">
                <p class="details__old-price">45 000 </p>
                <p class="details__price"> 
                  <span class="details__price-num">70 500 </span>
                </p>
              </div>
              <Button
                small
                class="details__btn"
                @click.native="showModal"
              >
                 
              </Button>
            </div>          
            <div class="details__features">
              <div class="details__features-icon">
                <Icon name="leaf" />
              </div>
              <p class="details__feature"> 006</p>
              <p class="details__feature"> 2 </p>
              <p class="details__feature"> 3D  </p>
            </div>
          </div>
          <p class="details__note">
            <span class="details__note-icon">
              <Icon name="check" />
            </span>
               </p>
          <p class="details__desc">          .         -          .</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Back from './base/Back'
import Button from './base/Button'
import Icon from './base/Icon'
import Header from './Header'
import DetailSlider from './DetailSlider'

export default {
  name: 'Details',
  components: {
    Back,
    Button,
    Icon,
    Header,
    DetailSlider
  },
  methods: {
    showModal() {
      this.$store.commit('setModal', 'calc')
    }
  }
}
</script>

<style lang="scss">
.details {
  &__slider {
    margin: 0 (-$container-padding);
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }

  &__category {
    font-size: 12px;
    line-height: (14/12);
    text-decoration: underline;
    color: $color-gray-middle;
  }

  &__heading {
    margin-top: 14px;
    font-size: 20px;
    line-height: (24/20);
  }

  &__info {
    margin-top: 4px;
  }

  &__prices {    
    display: flex;
    align-items: center;
  }

  &__old-price {
    margin-right: 20px;    
    font-weight: 200;
    font-size: 18px;
    line-height: (14/12);
    text-decoration: line-through;
    color: $color-gray-middle;
  }

  &__price {
    font-weight: 300;
    font-size: 20px;
    line-height: (23/20);
    color: $color-blue;
  }

  &__price-num {
    font-size: 30px;
  }

  &__btn {
    margin-top: 8px;
    width: 100%;
  }

  &__features {
    position: relative;
    margin-left: 12px;
    margin-top: 40px;
    padding: 26px 46px;
    background-color: #EFEFEF;
  }

  &__features-icon {
    position: absolute;
    top: -25px;
    right: -8px;
    width: 33px;
    height: 33px;
    fill: $color-green;
  }

  &__feature {
    position: relative;
    margin-bottom: 20px;
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 12px;
    line-height: (16/12);
    color: $color-blue;

    &::before {
      content: "";
      position: absolute;
      top: 7px;
      right: 100%;
      margin-right: 20px;
      width: 38px;
      height: 2px;
      background-color: $color-blue;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__note {
    display: flex;
    margin-top: 27px;
    font-size: 12px;
    line-height: (14/12);
    color: $color-gray;
  }

  &__note-icon {
    flex-shrink: 0;
    width: 9px;
    height: 9px;
    margin-right: 4px;
  }

  &__desc {
    margin-top: 17px;
    font-size: 12px;
    line-height: (24/12);
    color: $color-gray-middle;
  }

  @include media(md) {
    &__slider {
      margin: 0 (-$container-padding-md);
    }

    &__nav {
      display: block;
      margin-top: 36px;
    }

    &__category {
      margin-top: 24px;
    }

    &__heading {
      margin-top: 10px;
      font-size: 50px;
      line-height: (61/50);
    }

    &__info {
      display: flex;
      align-items: flex-start;
      margin-top: 6px;
    }

    &__cost {
      flex: 1;
      margin-right: 20px;
    }

    &__old-price {
      margin-top: 16px;
      margin-right: 36px;
      font-size: 28px;
      line-height: (46/28);
    }

    &__price {
      font-size: 22px;
      line-height: (36/22);
    }

    &__price-num {
      font-size: 50px;
    }

    &__btn {
      margin-top: 26px;
      width: auto;
      min-width: 280px;
    }

    &__features {
      margin-top: 10px;
      padding: 26px 34px;
    }

    &__features-icon {
      top: -8px;
      right: -2px;
    }

    &__feature::before {
      margin-right: 14px;
      width: 28px;
    }

    &__note {
      margin-top: 44px;
    }

    &__desc {
      margin-top: 40px;
      font-size: 14px;
      line-height: (29/14);
    }
  }

  @include media(lg) {
    &__slider {
      margin-top: 30px;
    }

    &__heading {
      margin-top: 16px;
    }

    &__info {
      padding-right: 70px;
    }

    &__features {
      margin-top: 24px;
      padding: 26px 30px;
    }

    &__note {
      margin-top: 38px;
    }

    &__desc {
      margin-top: 52px;
      padding-right: 30px;
    }
  }

  @include media(xl) {
    &__nav {
      margin-top: 56px;
    }

    &__heading {
      margin-top: 12px;
    }

    &__info {
      padding-right: 36px;
    }

    &__features {
      margin-top: 10px;
    }

    &__features-icon {
      top: -26px;
    }

    &__desc {
      margin-top: 44px;
    }

    &__slider-col {
      order: 1;
    }
    
    &__slider {
      margin-top: 0;
      margin-left: 0;
      margin-right: -$container-padding-xl;
    }
  }
}
</style>
