<template>
  <header class="header">
    <RouterLink
      to="/"
      exact
      class="header__logo"
    >
      <img src="../assets/img/logo.svg" alt="Milis">
    </RouterLink>
    <div class="header__actions" v-if="!mobile">
      <nav class="header__nav">
        <div class="header__nav-item">          
          <RouterLink class="header__link" to="/catalog">
            <span class="header__nav-arrow">
              <Icon name="angle-right"/>
            </span>
            
          </RouterLink>
          <Menu class="header__menu" />
        </div>
        <div class="header__nav-item">
          <RouterLink class="header__link" to="/about"></RouterLink>
        </div>
        <div class="header__nav-item">
          <RouterLink class="header__link" to="/delivery">  </RouterLink>
        </div>
        <div class="header__nav-item">
          <RouterLink class="header__link" to="/payment"></RouterLink>
        </div>
      </nav>
      <div class="header__contacts">
        <a href="#" class="header__city">-</a>
        <a href="tel:+78009859598" class="header__phone">
          <span class="header__phone-icon">
            <Icon name="phone"/>
          </span>
          +7 (800) 985-95-98
        </a>
      </div>
    </div>
    <RouterLink
      to="/blog"
      v-slot="{ href, navigate }"
      v-if="main && mobile"
    >
      <ButtonAlt
        class="header__btn"
        :href="href"
        text=" "
        :shadow="info"
        @click.native="navigate"
      />
    </RouterLink>
  </header>
</template>

<script>
import Icon from './base/Icon'
import ButtonAlt from './base/ButtonAlt'
import Menu from './Menu'

export default {
  name: 'Header',
  components: {
    Icon,
    ButtonAlt,
    Menu
  },
  props: {
    main: Boolean,
    info: Boolean
  },
  computed: {
    mobile() {
      return this.$store.state.mobile
    }
  }
}
</script>

<style lang="scss">
.header {
  $b: &;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  font-family: $font-secondary;

  &__logo {
    flex-shrink: 0;
    width: 69px;
  }

  &__actions {
    padding: 14px;
  }

  &__nav {
    display: flex;
  }

  &__nav-item {
    position: relative;
    margin-right: 24px;
    font-size: 12px;
    line-height: (15/12);
    color: $color-blue;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      #{$b}__menu {
        opacity: 1;
        pointer-events: all;
        visibility: visible;
        transition: opacity .3s ease, visibility 0s;
      }
    }
  }

  &__link {
    display: flex;

    &.is-active {
      font-weight: bold;
    }
  }

  &__nav-arrow {
    width: 8px;
    height: 8px;
    margin-top: 2px;
    margin-right: 3px;
  }

  &__menu {
    position: absolute;
    left: 0;
    top: 100%;
    width: 900px;
    box-shadow: 0px 0px 17px rgba(0, 0, 0, 0.25);
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: opacity .3s ease, visibility 0s .3s;
    z-index: 10;
  }

  &__contacts {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 20px;
  }

  &__city {
    margin-right: 18px;
    font-size: 10px;
    line-height: (12/10);
    text-decoration: underline;
    color: $color-gray;
  }

  &__phone {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 14px;
    line-height: (17/14);
    text-decoration: underline;
    color: $color-blue;
  }

  &__phone-icon {
    width: 15px;
    height: 15px;
    margin-right: 4px;
    fill: $color-green;
  }

  @include media(md) {
    padding-top: 24px;
    
    &__logo {
      width: 80px;
    }
  }

  @include media(lg) {
    align-items: flex-start;
    padding-top: 28px;

    &__logo {
      width: 100px;
    }
  }

  @include media(xl) {
    padding-top: 43px;

    &__logo {
      width: 132px;
    }

    &__nav-item {
      margin-right: 46px;
      font-size: 14px;
      line-height: (17/14);
    }

    &__nav-arrow {
      width: 12px;
      height: 12px;
    }

    &__menu {
      width: 1207px;
    }

    &__city {
      margin-right: 50px;
      font-size: 12px;
      line-height: (15/12);
    }

    &__phone {
      font-size: 16px;
      line-height: (20/16);
    }

    &__phone-icon {
      width: 20px;
      height: 20px;
    }
  }
}
</style>
