<template>
  <div class="questions">
    <div class="questions__row">
      <div class="questions__col">
        <p class="questions__item">   ,  {{'\xa0'}}?</p>
        <p class="questions__item">  ?</p>
      </div>
      <div class="questions__col">
        <p class="questions__item">  ?</p>
        <p class="questions__item">    ?</p>
      </div> 
    </div>
    <div class="questions__overlay js-anim" v-if="!$mobile">
    </div>
    <div class="questions__mark js-anim">
      <Icon name="question" />
    </div>
    <div class="questions__leaf">
      <Icon name="leaf" />
    </div>
  </div>
</template>

<script>
import Icon from './base/Icon'

export default {
  name: 'Questions',
  components: {
    Icon
  }
}
</script>

<style lang="scss">
.questions {
  $b: &;

  position: relative;
  padding: 20px 30px 26px;
  font-family: $font-secondary;
  background-color: $color-blue;

  &__col {
    margin-bottom: 16px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__item {
    margin-bottom: 16px;
    font-weight: bold;
    font-size: 12px;
    line-height: (20/12);
    color: $color-white;
    
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__mark {
    position: absolute;
    left: -20px;
    bottom: -20px;
    width: 48px;
    height: 48px;
  }

  &__leaf {
    position: absolute;
    top: 30px;
    right: -10px;
    width: 50px;
    height: 50px;
    fill: $color-green;
    z-index: 1;
  }

  @include media(md) {
    padding: 38px 26px 46px;

    &__item {
      font-size: 16px;
      line-height: (20/16);
    }

    &__mark {
      left: -24px;
      bottom: -24px;
    }

    &__leaf {
      right: -40px;
      width: 100px;
      height: 100px;
    }
  }

  @include media(lg) {
    padding: 20px ($container-padding-md) 20px 16px;

    &__row {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__col {
      width: 45%;
      padding-right: 20px;
      margin-bottom: 0;
    }

    &__item {
      font-size: 12px;
      line-height: (15/12);

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__overlay {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      overflow: hidden;
      pointer-events: none;

      &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        width: 105%;
        padding-top: 100%;
        border-top-left-radius: 100%;
        background-color: $color-white;
        transition: transform .75s ease;
      }

      &.show {
        &::after {
          transform: translateY(100%);
        }
      }
    }

    &__mark {
      left: -16px;
      bottom: -10px;
      width: 32px;
      height: 32px;
      opacity: 0;
      transition: opacity .5s ease .75s;

      &.show {
        opacity: 1;
      }
    }

    &__leaf {
      top: auto;
      right: -26px;
      bottom: -26px;
      width: 68px;
      height: 68px;
    }
  }

  @include media(xl) {
    padding: 38px $container-padding-xl 40px 26px;

    &__col {
      width: auto;

      &:nth-child(2n) {
        padding-right: 0;
      }
    }

    &__item {
      font-size: 16px;
      line-height: (20/16);
    }

    &__mark {
      left: -24px;
      bottom: -14px;
      width: 48px;
      height: 48px;
    }

    &__leaf {
      bottom: -40px;
      width: 100px;
      height: 100px;
    }
  }
}
</style>
