<template>
  <form :class="['call-form', {'call-form_modal': modal}]">
    <p class="call-form__title">    </p>
    <p class="call-form__desc">     .  ,      {{'\xa0'}}           20 </p>
    <div class="call-form__items">
      <div class="call-form__fields">
        <TextInput
          label=""
          class="call-form__field"
        />
        <TextInput
          label=""
          class="call-form__field"
        />
        <div class="call-form__dates">
          <label class="call-form__date" @click="showTimes">
            <input type="radio" name="date" class="call-form__date-radio">
            <span class="call-form__date-box"><b></b><br>12 </span>            
          </label>
          <label class="call-form__date" @click="showTimes">
            <input type="radio" name="date" class="call-form__date-radio">
            <span class="call-form__date-box"><b></b><br>13 </span>            
          </label>
          <label class="call-form__date" @click="showTimes">
            <input type="radio" name="date" class="call-form__date-radio">
            <span class="call-form__date-box"><b></b><br>14 </span>            
          </label>
        </div>
        <div
          class="call-form__times"
          :class="{'is-active': activeTimes}"
          ref="times"
        >
          <p class="call-form__times-title"> </p>
          <label class="call-form__time">
            <input type="radio" name="time" class="call-form__time-radio">
            <span class="call-form__time-box">09:00 - 12:00</span>
            <span class="call-form__time-check">
              <Icon name="check-circle"/>
            </span>
          </label>
          <label class="call-form__time is-active">
            <input type="radio" name="time" class="call-form__time-radio">
            <span class="call-form__time-box">12:00 - 15:00</span>
            <span class="call-form__time-check">
              <Icon name="check-circle"/>
            </span>
          </label>
          <label class="call-form__time">
            <input type="radio" name="time" class="call-form__time-radio">
            <span class="call-form__time-box">15:00 - 18:00</span>
            <span class="call-form__time-check">
              <Icon name="check-circle"/>
            </span>
          </label>
          <label class="call-form__time">
            <input type="radio" name="time" class="call-form__time-radio">
            <span class="call-form__time-box">18:00 - 21:00</span>
            <span class="call-form__time-check">
              <Icon name="check-circle"/>
            </span>
          </label>
        </div>
        <Button
          blue
          md
          class="call-form__btn"
        >
          
        </Button>
      </div>
      <div class="call-form__features">
        <p class="call-form__feature">
          <span class="call-form__feature-icon">
            <Icon name="money"/>
          </span>
            
        </p>
        <p class="call-form__feature">
          <span class="call-form__feature-icon">
            <Icon name="ruler"/>
          </span>
            
        </p>
        <p class="call-form__feature">
          <span class="call-form__feature-icon">
            <Icon name="paint"/>
          </span>
               250{{'\xa0'}}   
        </p>
        <p class="call-form__feature">
          <span class="call-form__feature-icon">
            <Icon name="brilliant"/>
          </span>
              
        </p>
        <p class="call-form__decor" v-if="$windowWidth >= $breakpoints.md && $windowWidth < $breakpoints.lg"> !</p>
      </div>
    </div>
  </form>
</template>

<script>
import TextInput from './base/TextInput'
import Icon from './base/Icon'
import Button from './base/Button'

export default {
  name: 'CallForm',
  components: {
    TextInput,
    Icon,
    Button
  },
  props: {
    modal: Boolean
  },
  data: () => ({
    activeTimes: false
  }),
  methods: {
    showTimes() {
      if (this.activeTimes) {
        return
      }

      this.activeTimes = true
      const timesRef = this.$refs.times
      timesRef.style.height = timesRef.scrollHeight + 'px'

      setTimeout(() => {
        timesRef.style.height = 'auto'
      }, 300);
    }
  }
}
</script>

<style lang="scss">
.call-form {
  $b: &;

  &_modal {
    #{$b} {
      &__title {
        padding-right: 50px;
        margin-bottom: 20px;
      }

      &__field {
        background-color: $color-white;
      }
    }
  }

  &__title {
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 24px;
    line-height: (32/24);
    color: $color-blue;
  }

  &__desc {
    margin-top: 10px;
    font-size: 11px;
    line-height: (17/11);
    color: $color-gray;
  }

  &__items {
    margin-top: 25px;
  }

  &__field {
    margin-top: 24px;
    background-color: #F9F9F9;

    &:first-child {
      margin-top: 0;
    }
  }

  &__dates {
    display: flex;
    margin: 26px -3px 0;
  }

  &__date {
    display: block;
    flex-grow: 1;
    margin: 0 3px;
    font-family: $font-secondary;
    cursor: pointer;
  }

  &__date-radio {
    display: none;

    &:checked + #{$b}__date-box {
      background-color: $color-green;
    }
  }

  &__date-box {
    display: block;
    padding: 6px;
    text-align: center;
    font-size: 11px;
    line-height: (14/11);
    color: $color-white;
    background-color: $color-blue;
    transition: background-color .3s ease;
  }

  &__times {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px -10px;
    height: 0;
    padding-top: 18px;
    opacity: 0;
    overflow: hidden;
    transition: opacity .3s ease, height .3s ease;

    &.is-active {
      opacity: 1;
    }
  }

  &__times-title {
    width: 100%;
    margin-left: 10px;
    margin-bottom: 9px;
    font-size: 11px;
    line-height: (17/11);
    color: $color-gray;
  }

  &__time {
    display: block;
    position: relative;
    flex-grow: 1;
    flex-basis: 35%;
    margin: 0 10px 10px;
    font-family: $font-secondary;
    cursor: pointer;
  }

  &__time-radio {
    display: none;

    &:checked ~ #{$b}__time-box {
      border-color: $color-green;
    }

    &:checked ~ #{$b}__time-check {
      opacity: 1;
    }
  }

  &__time-box {
    display: block;
    border: 1px solid $color-gray;
    padding: 5px;
    text-align: center;
    font-size: 12px;
    line-height: (16/12);
    color: $color-blue;
    transition: border-color .3s ease;
  }

  &__time-check {
    position: absolute;
    top: -7px;
    right: -5px;
    width: 11px;
    height: 11px;
    opacity: 0;
    pointer-events: none;
    transition: opacity .3s ease;
  }

  &__btn {
    margin-top: 28px;
    width: 100%;
  }

  &__features {
    margin-top: 26px;
    padding-right: 20px;
  }

  &__feature {
    display: flex;
    align-items: center;
    margin-bottom: 23px;
    font-family: $font-secondary;
    font-weight: bold;
    font-size: 12px;
    line-height: (16/12);
    color: $color-blue;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__feature-icon {
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    margin-right: 14px;
  }

  &__decor {
    display: none;
    margin-top: -6px;
    margin-left: 6px;
    font-family: $font-decorative;
    font-size: 50px;
    color: $color-gray-middle;
  }

  @include media(md) {
    &_modal {
      #{$b} {
        &__title {
          margin-bottom: 0;
        }

        &__desc {
          font-size: 11px;
          line-height: (17/11);
        }
      }
    }

    &__desc {
      font-size: 12px;
      line-height: (19/12);
      max-width: 530px;
    }

    &__items {
      display: flex;
      margin-top: 22px;
    }

    &__fields {
      width: 57%;
      margin-right: 26px;
    }

    &__times {
      margin: 0 -6px;
    }

    &__times-title {
      margin-left: 6px;
    }

    &__time {
      flex-basis: auto;
      margin: 0 6px;
    }

    &__features {
      flex: 1;
      margin-top: 56px;
      padding-right: 0;
    }

    &__feature {
      margin-bottom: 38px;
    }

    &__feature-icon {
      width: 24px;
      height: 24px;
      margin-right: 8px;
    }

    &__decor {
      display: block;
    }
  }

  @include media(lg) {
    &_modal {
      #{$b} {
        &__title {
          padding-right: 0;
        }

        &__items {
          display: block;
          margin-right: 105px;
        }

        &__fields {
          width: 100%;
        }

        &__features {
          margin-top: 26px;
        }

        &__feature {
          margin-bottom: 22px;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

    &__fields {
      width: 50%;
      margin-right: 60px;
    }

    &__feature {
      font-size: 14px;
      line-height: (19/14);
    }

    &__feature-icon {
      margin-right: 12px;
    }
  }

  @include media(xl) {
    &_modal {
      #{$b} {
        &__items {
          display: flex;
          margin-right: 0;
        }

        &__fields {
          width: 51%;
        }

        &__features {
          margin-top: 56px;
        }

        &__feature {
          margin-bottom: 38px;
        }
      }
    }
  }
}
</style>