        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"> </a>
      </template>
      <template v-if="technics">
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"></a>
        <a href="#" class="filter-menu__item"> </a>
      </template>
      <template v-if="reviews">
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item">  </a>
      </template>
      <template v-if="blog || article">
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"></a>
        <a href="#" class="filter-menu__item"></a>
        <a href="#" class="filter-menu__item"> </a>
        <a href="#" class="filter-menu__item"></a>
        <a href="#" class="filter-menu__item"> </a>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FilterMenu',
  props: {
    catalog: Boolean,
    technics: Boolean,
    reviews: Boolean,
    blog: Boolean,
    article: Boolean
  }
}
