<style lang="scss">
.filter-menu {
  overflow: hidden;

  &__wrapper {
    display: flex;
    margin-bottom: -20px;
    padding-bottom: 20px;
    overflow-x: scroll;

    &::before,
    &::after {
      content: "";
      flex-shrink: 0;
      width: $container-padding;
    }
  }

  &__item {
    flex-shrink: 0;
    margin-right: 10px;
    padding: 7px 14px;
    border-radius: 85px;
    font-size: 12px;
    line-height: (14/12);
    background-color: $color-gray-6;

    &:last-child {
      margin-right: 0;
    }
  }

  @include media(md) {
    &__wrapper {
      &::before,
      &::after {
        width: $container-padding-md;
      }
    }

    &__item {
      padding: 8px 19px;
      font-size: 14px;
      line-height: (16/14);
    }
  }

  @include media(lg) {
    overflow: visible;

    &__wrapper {
      flex-wrap: wrap;
      margin-bottom: -26px;
      padding-bottom: 0;
      overflow-x: visible;

      &::before,
      &::after {
        display: none;
      }
    }

    &__item {
      margin-right: 26px;
      margin-bottom: 26px;
      padding: 0;
      font-size: 14px;
      line-height: (16/14);
      text-decoration: underline;
      color: $color-gray;
      background-color: transparent;
    }
  }

  @include media(xl) {
    &__item {
      margin-right: 48px;
    }
  }
}
</style>
